import React from "react";

const Footer = ({ bg }) => {
  return (
    <div
      style={{
        display: "flex",
        padding: "5rem",
    
        marginBottom: "4rem",
        background: bg,
        transition: "1s",
        justifyContent: "center",
      }}
    >
      <h5 style={{textTransform: "uppercase" }}>
        all rights reserved by aman dubey and co.
      </h5>
    </div>
  );
};

export default Footer;
