import React, { useState, createContext } from "react";
import { NavLink } from "react-router-dom";
import "./Style/Headers.css";
import logo from "../Images/logo.png";
import { themeState } from "../Context/ThemeProvider";

const Header = () => {
  const {darkTheme,SetdarkTheme} = themeState()
  return (
    <div
      className="nav"
      style={{
        transition: "1s",
        background: `${!darkTheme ? "pink" : " rgb(52, 54, 54)"}`,
      }}
    >
      <div className="nav_logo">
        <NavLink to="/">
          <img
            src={logo}
            className="logoimg"
            style={{ borderRadius: ".4rem" }}
          />
        </NavLink>
        <div class="checkbox-wrapper-54">
          <label class="switch">
            <input type="checkbox" onClick={()=>SetdarkTheme(!darkTheme)} />
            <span class="slider"></span>
          </label>
        </div>
      </div>

      <div className="nav_links">
        <NavLink to="/" className="links morelink" type="button">
          Home
        </NavLink>
        <NavLink to="/company" className="links" type="button">
          Our Company.
        </NavLink>
      </div>
    </div>
  );
};

export default Header;
