import React from "react";
import Layout from "../Components/Layout";
import { NavLink } from "react-router-dom";

const Company = () => {
  let links = [
    {
      Name: "Ecommerce - JWT,MERNSTACK,Encryption,Authorization",
      Link: "https://cake-n-bake2.onrender.com",
    },
    {
      Name: "AiStudio - OpenAi,REACT,APIs,UI",
      Link: "https://aistudio.onrender.com",
    },
    {
      Name: "PortFolio - REACT , UI , MyWorks,AboutMe",
      Link: "https://aman-dubey-2.onrender.com",
    },
    {
      Name: "ChatApp - Socket.io ,MERN,Authentication,Authorization ",
      Link: "https://butterfly-w0aw.onrender.com",
    },
    {
      Name: "Dashboard - UI,Graphs,responsiveness",
      Link: "https://reactdashboard-ddew.onrender.com/",
    },
  ];
  return (
    <div>


      <Layout title="Company and details">
        <div style={{ textAlign: "center" }}>


          <h3>Hey there! My-self <b>Aman Dubey, </b>  </h3>{" "}
          <p>
            i build this web site just for the pratice in the ui,OpenAi Apis,and
            just brushing my hands in React
          </p>
          <p>i am aware of that i have not used any Authentication,Authorization,any Advance concept but the Graphs</p>
        </div>

        <div style={{ padding: "2rem 3rem" }}>
          {
            <div>
              <h2 style={{ paddingBottom: "1rem", display: "flex", justifyContent: "center" }}> My Works</h2>
              <div style={{ display: "flex", flexDirection: "column", alignItems: "center", gap: "2px" }}>
                {links.map((item, i) => {
                  return (
                    <div style={{ marginBottom: "2rem", border: "1px ", }} key={i}>
                      <NavLink
                        to={item.Link}
                        className="mylinks"
                        style={{ border:"1px solid black", color: "black", textDecoration: "none", padding: ".4rem 1rem", borderRadius: "4px", }}
                        target="_blank"
                      >
                        {" "}
                        {item.Name}
                      </NavLink>
                    </div>
                  );
                })}
              </div>
            </div>
          }
        </div>
      </Layout>
    </div>
  );
};

export default Company;
