import React, { useEffect, useState } from "react";
import Layout from "../Components/Layout";
import "./Style/Home.css";
import { NavLink } from "react-router-dom";
// import logo from "../Images/logo.png";
import axios, { Axios } from "axios";
import toast from "react-hot-toast";
const Home = () => {
  const [photo, Setphoto] = useState([]);
  useEffect(() => {
    const GetPhoto = async () => {
      try {
        const { data } = await axios.get(
          `https://api.unsplash.com/photos/?client_id=X3EG7HBjV-lEWHjsrgslYhiO2-Hk2YwSJ9AzIwh8ymQ`
        );
        if (data) {
          Setphoto(data.slice(0,10));
          toast.success("wlcome");
        } else {
          toast.error("server not responded");
        }
      } catch (error) {
        toast.error("something went wrong");
      }
    };
    GetPhoto();
  }, []);
  return (
    <Layout title="home page" description="Generate realistic photos from text descriptions." keywords="text to image ai free download">
      <div className="home">
        <div className="home_left">
          <div className="heading">
            <h2 className="greeting">Welcome to AI Studio </h2>
            <h4 className="para1">
              {" "}
              AI Studio is a website that lets you create stunning photos and
              videos with the power of artificial intelligence. Whether you want
              to make a personal project, a professional presentation, or a
              viral social media post, AI Studio has the tools you need to
              unleash your creativity.
            </h4>
            <h4 className="para2">
              Generate realistic photos from text descriptions.
            </h4>{" "}
            .{" "}
            <div className="buttons">
              <NavLink to="/ai" className="button-50">
                Ai image generator
              </NavLink>
              <NavLink to="/normal" className="button-50">
                 Image generator
              </NavLink>
            </div>
          </div>
        </div>
        <div className="home_right">
          <div className="imagessection">
            {photo.map((data) => {
              return (
                <div key={data.id} className="homeimgbox">
                  <img
                  className="homeimgs"
                    src={data.urls.small}
                    height={90}
                    style={{ borderRadius: "6px" }}
                  />
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Home;
