import React, { useEffect, useState } from "react";
import Layout from "../Components/Layout";
import "./Style/Ai_img_generator.css";

import { NavLink } from "react-router-dom";
import { toast } from "react-hot-toast";
import { Button, Image } from "antd";
import normalbg from "../Images/normal bg.png";
import axios from "axios";

const AI_img = () => {
  const [Aiimage, SetAiimage] = useState([]);
  const [userprompt, Setuserprompt] = useState("");
  const [loading, Setloading] = useState(false);
  const [pages, Setpages] = useState(0);
  const [totalimages, Settotalimages] = useState(0);

  const images_per_page = 15;
  const API_URL = "https://api.unsplash.com/search/photos";

  const GenerateImage = async () => {
    try {
      if (userprompt) {
        Setloading(true);
        const { data } = await axios.get(
          `https://api.unsplash.com/search/photos?query=${userprompt}&page=${pages}&per_page=${images_per_page}&client_id=${process.env.REACT_APP_UNSPLASH_KEY}`
        );
        if (data) {
          SetAiimage(data.results);
          Settotalimages(data.total_pages);
          Setloading(false);
        }
      }
    } catch (error) {
      toast.error("something went wrong");
      Setloading(false);
    }
  };

  useEffect(() => {
    GenerateImage();
  }, [pages]);

  const ResetSearch = () => {
    Setpages(1);
    GenerateImage();
  };
  const HandleSearch = (event) => {
    event.preventDefault();
    ResetSearch();
  };
  const HandleSelection = (selection) => {
    Setuserprompt(selection);
    ResetSearch();
  };

  return (
    <div>
      <Layout title=" Image Generator">
        <div className="loading">
          <div className={loading ? "loading-bar-full" : "loading-bar"}></div>
          <div className={loading ? "loading-text" : "display-none"}>
            loading...
          </div>
        </div>
        <div className="ai_generator">
          <h1 className="ai_heading">
            4K Image <span style={{ color: "black" }}>Generator</span>{" "}
          </h1>
          <h5>powered by unsplash</h5>
          <div>
            <img
              src={normalbg}
              className="defaultImage"
              style={{ borderRadius: "1rem" }}
            />
          </div>

          <form className="ai_input_form" onSubmit={HandleSearch}>
            <input
              type="text"
              value={userprompt}
              className="ai_input"
              onChange={(e) => Setuserprompt(e.target.value)}
              placeholder="Type Keywords : eg cars "
            />
            <button className="button-85 ">generate</button>
          </form>
          <div className="selection">
            <button
              className="button-33"
              value="Birds"
              onClick={(e) => {
                HandleSelection("Birds");
              }}
            >
              Birds
            </button>
            <button
              className="button-33"
              value="Man"
              onClick={() => {
                HandleSelection("Man");
              }}
            >
              Man
            </button>
            <button
              className="button-33"
              value="Corgi"
              onClick={(e) => {
                HandleSelection("Corgi");
              }}
            >
              corgi
            </button>
            <button
              className="button-33"
              value="New York"
              onClick={(e) => {
                HandleSelection("New York");
              }}
            >
              New York
            </button>
          </div>
          <NavLink to="/ai">Ai Feature</NavLink>
        </div>

        {
          Aiimage ?  <Image.PreviewGroup>
          <div className="image_gllary">
            {Aiimage.map((data) => {
              return (
                <div className="images" key={data.id}>
                  <Image
                    key={data.id}
                    src={data.urls.small}
                    className="img"
                    alt={data.slug}
                    height={150}
                  />
                </div>
              );
            })}
          </div>
        </Image.PreviewGroup>:<div className="d-flex justify-content-center align-items-center">
          <span style={{fontSize:"60px"}}><FcCancel/></span>
          </div>
        }
       


        <div className="pagination">{pages > 1 && <h2>page :{pages}</h2>}</div>
        <div className="navigations">
          {pages > 1 && (
            <NavLink onClick={() => Setpages(pages - 1)} className="button-50">
              previous
            </NavLink>
          )}
          {pages < totalimages && (
            <NavLink className="button-50" onClick={() => Setpages(pages + 1)}>
              Next
            </NavLink>
          )}
        </div>
      </Layout>
    </div>
  );
};

export default AI_img;
