import React, { createContext, useState } from "react";
import { Helmet } from "react-helmet";
import Header from "./Header";
import Footer from "./Footer";
import { Toaster } from "react-hot-toast";
import { useContext } from "react";
import { themeState } from "../Context/ThemeProvider";

const themeStyle = createContext();
const Layout = ({ title, description, keywords, author, children }) => {
  const {darkTheme,SetdarkTheme} = themeState()


  return (
    <div>
      <Helmet>
        <meta charSet="utf-8" />
        <meta name="description" content={description} />
        <meta name="keywords" content={keywords} />1
        <meta name="author" content={author} />
        <title>{title}</title>
      </Helmet>
      <Header/>
      <Toaster />
      <main
        style={{
          transition: "1s",
          background: `${!darkTheme ? "pink" : " rgb(52, 54, 54)"}`,
        
        }}
      >
        {children}
      </main>
      <Footer bg={!darkTheme ? "pink" : " rgb(52, 54, 54)"} />
    </div>
  );
};

export default Layout;
