import React, { useState } from "react";
import Layout from "../Components/Layout";
import "./Style/Ai_img_generator.css";
import AIIMG from "../Images/aibanner.jpeg";
import { NavLink } from "react-router-dom";
import { toast } from "react-hot-toast";
import { useRef } from "react";
import { Image } from "antd";

const AI_img = () => {
  const [Aiimage, SetAiimage] = useState("");
  const [loading, Setloading] = useState(false);
  const inputRef = useRef(null);

  const GenerateImage = async (e) => {
    e.preventDefault();
    try {
      if (inputRef.current.value === "") {
        toast.error(" Please! add some keyword to the prompt");
        return 0;
      }
      console.log(process.env);
      Setloading(true);
      const response = await fetch(
        "https://api.openai.com/v1/images/generations",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${process.env.REACT_APP_OPENAI_KEY}`,
            "User-Agent": "Chrome",
          },
          body: JSON.stringify({
            prompt: `${inputRef.current.value}`,
            n:1,
            size: "512x512",
          }),
        }
      );
      let data = await response.json();
      console.log(data);
      let data_array = data.data;
      SetAiimage(data_array[0].url);
      Setloading(false);
    } catch (error) {
      toast.error("something went wrong");
      Setloading(false);
    }
  };

  return (
    <div>
      <Layout title="AI Image Generator">
        <div className="loading">
          <div className={loading ? "loading-bar-full" : "loading-bar"}></div>
          <div className={loading ? "loading-text" : "display-none"}>
            loading...
          </div>
        </div>
        <div className="ai_generator">
          <h1 className="ai_heading">
            Ai Image <span style={{ color: "white" }}>Generator</span>{" "}
          </h1>
          <h5>powered by Open AI</h5>
          <div>
            <img
              src={Aiimage ? Aiimage : AIIMG}
              className="defaultAiImage"
              style={{ borderRadius: "2rem" }}
            />
           
          </div>
          <p style={{fontWeight:"bold",textTransform:"capitalize"}}>it has limit dont use it frequently or it will not work</p>
          <form className="ai_input_form" onSubmit={GenerateImage}>
            <input
              type="text"
              className="ai_input"
              ref={inputRef}
              placeholder="  Type keywords"
            />
            <button className="button-85 ">generate</button>
          </form>
          <NavLink to="/normal">Normal Feature</NavLink>
        </div>
      </Layout>
    </div>
  );
};

export default AI_img;
